import React from 'react';
import { makeStyles, Grid, Card, CardHeader, CardContent, Hidden, Typography, Divider, LinearProgress } from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import { useState, useEffect } from 'react';
import HotelTwoToneIcon from '@material-ui/icons/HotelTwoTone';
import HowToRegTwoToneIcon from '@material-ui/icons/HowToRegTwoTone';
import FavoriteTwoToneIcon from '@material-ui/icons/FavoriteTwoTone';
import SentimentVeryDissatisfiedTwoToneIcon from '@material-ui/icons/SentimentVeryDissatisfiedTwoTone';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import TrendingDownIcon from '@material-ui/icons/TrendingDown';

import VaccinatedLineCard from './VaccinatedLineCard';
import VaccinatedLineData from './chart/sale-chart-1';

import RevenuChartCard from './RevenuChartCard';
import RevenuChartCardData from './chart/revenu-chart';

import CovidDataCard from './CovidDataCard';
import StateWiseDataCard from './StateWiseDataCard';

import { DATA_API_ROOT, gridSpacing, numDifferentiation } from './../../store/constant';
const DATA_URL = `${DATA_API_ROOT}/data.min.json`;

const useStyles = makeStyles((theme) => ({
    arrowicon: {
        '& svg': {
            width: '20px',
            height: '20px',
            verticalAlign: 'top',
        },
    },
    flatcardbody: {
        padding: '0px !important',
        '& svg': {
            width: '40px',
            height: '40px',
        },
    },
    flatcardblock: {
        padding: '25px 25px',
        borderLeft: '1px solid' + theme.palette.background.default,
        [theme.breakpoints.down('xs')]: {
            borderLeft: 'none',
            borderBottom: '1px solid' + theme.palette.background.default,
        },
        [theme.breakpoints.down('sm')]: {
            borderBottom: '1px solid' + theme.palette.background.default,
        },
    },
    textsuccess: {
        color: theme.palette.success.main,
    },
    texterror: {
        color: theme.palette.error.main,
    },
}));

const Default = () => {
    const classes = useStyles();
    const theme = useTheme();

    const [dataTotal, setTotal] = useState([]);
    const [dataDelta, setDelta] = useState([]);
    const [dataMeta, setMeta] = useState([]);

    const getData = () => {
        fetch(DATA_URL)
          .then((response) => {
            return response.json();
          })
          .then((json) => {
            setTotal(json.TT.total);
            setDelta(json.TT.delta);
            setMeta(json.TT.meta);
          });
      }
      
    useEffect(() => {
        getData();
    }, []);

    const conf = numDifferentiation(dataTotal.confirmed);
    const recv = numDifferentiation(dataTotal.recovered);
    const decs = numDifferentiation(dataTotal.deceased);
    const actv = numDifferentiation(dataTotal.confirmed - dataTotal.recovered - dataTotal.deceased);
    const confDelta = parseInt(dataDelta.confirmed).toLocaleString('en-IN');
    const recvDelta = parseInt(dataDelta.recovered).toLocaleString('en-IN');
    const decsDelta = parseInt(dataDelta.deceased).toLocaleString('en-IN');
    const actvDelta = parseInt(dataDelta.confirmed - dataDelta.recovered - dataDelta.deceased).toLocaleString('en-IN');
    const positivityRatio = ((dataTotal.confirmed / dataTotal.tested)*100).toFixed(2);
    const activeRatio = (((dataTotal.confirmed - dataTotal.recovered - dataTotal.deceased) / dataTotal.confirmed)*100).toFixed(2);
    const recoveredRatio = ((dataTotal.recovered / dataTotal.confirmed)*100).toFixed(2);
    const fatalityRatio = ((dataTotal.deceased / dataTotal.confirmed)*100).toFixed(2);
    const vaccinatedRatio = ((dataTotal.seconddosevaccinated / dataMeta.population)*100).toFixed(2)

    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item lg={3} sm={6} xs={6}>
                        <CovidDataCard
                            primary={conf}
                            secondary="Confirmed"
                            color={theme.palette.error.main}
                            footerData={confDelta}
                            iconPrimary={HotelTwoToneIcon}
                            iconFooter={TrendingDownIcon}
                        />
                    </Grid>
                    <Grid item lg={3} sm={6} xs={6}>
                        <CovidDataCard
                            primary={actv}
                            secondary="Active"
                            color={theme.palette.primary.main}
                            footerData={actvDelta}
                            iconPrimary={HowToRegTwoToneIcon}
                            iconFooter={TrendingUpIcon}
                        />
                    </Grid>
                    <Grid item lg={3} sm={6} xs={6}>
                        <CovidDataCard
                            primary={recv}
                            secondary="Recovered"
                            color={theme.palette.success.main}
                            footerData={recvDelta}
                            iconPrimary={FavoriteTwoToneIcon}
                            iconFooter={TrendingUpIcon}
                        />
                    </Grid>
                    <Grid item lg={3} sm={6} xs={6}>
                        <CovidDataCard
                            primary={decs}
                            secondary="Deceased"
                            color={theme.palette.warning.main}
                            footerData={decsDelta}
                            iconPrimary={SentimentVeryDissatisfiedTwoToneIcon}
                            iconFooter={TrendingUpIcon}
                        />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item lg={8} xs={12}>
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12} sm={6}>
                                <Grid container spacing={gridSpacing} direction="column">
                                    <Grid item xs={12}>
                                        <VaccinatedLineCard
                                            chartData={VaccinatedLineData}
                                            title="Vaccine Dose Administrated"
                                            vaccinedose={numDifferentiation(dataTotal.vaccinated)}
                                            footerData={[
                                                {
                                                    value: numDifferentiation(dataTotal.firstdosevaccinated),
                                                    label: 'First Dose',
                                                },
                                                {
                                                    value: numDifferentiation(dataTotal.seconddosevaccinated),
                                                    label: 'Second Dose',
                                                },
                                            ]}
                                        />
                                    </Grid>
                                    <Hidden only="sm">
                                        <Grid item xs={12}>
                                            <Card>
                                                <CardContent className={classes.flatcardbody}>
                                                    <Grid container alignItems="center" spacing={0}>
                                                        <Grid item sm={6} xs={12} className={classes.flatcardblock}>
                                                            <Grid container alignItems="center" spacing={1}>
                                                                <Grid item>
                                                                    <Typography variant="subtitle2" align="left">
                                                                        vaccinated
                                                                    </Typography>
                                                                </Grid>
                                                                <Grid item sm zeroMinWidth>
                                                                    <Typography variant="h5" className={classes.texterror} align="right">
                                                                        {vaccinatedRatio}%
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                        </Grid>
                                                        <Grid item sm={6} xs={12} className={classes.flatcardblock}>
                                                            <Grid container alignItems="center" spacing={1}>
                                                                <Grid item>
                                                                    <Typography variant="subtitle2" align="left">
                                                                        Recovery
                                                                    </Typography>
                                                                </Grid>
                                                                <Grid item sm zeroMinWidth>
                                                                    <Typography variant="h5" className={classes.textsuccess} align="right">
                                                                        {recoveredRatio}%
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                </CardContent>
                                            </Card>
                                        </Grid>
                                    </Hidden>
                                </Grid>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <RevenuChartCard chartData={RevenuChartCardData} />
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item lg={4} xs={12}>
                        <Card>
                            <CardHeader
                                title={
                                    <Typography component="div" className="card-header">
                                        Total Ratios
                                    </Typography>
                                }
                            />
                            <Divider />
                            <CardContent>
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12}>
                                        <Grid container alignItems="center" spacing={1}>
                                            <Grid item sm zeroMinWidth>
                                                <Typography variant="body2">Test Positivity Ratio</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography variant="body2" align="right">
                                                    {positivityRatio}%
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={12}>
                                                <LinearProgress variant="determinate" value={parseInt(positivityRatio)} color="primary" />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Grid container alignItems="center" spacing={1}>
                                            <Grid item sm zeroMinWidth>
                                                <Typography variant="body2">Active Ratio</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography variant="body2" align="right">
                                                    {activeRatio}%
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={12}>
                                                <LinearProgress variant="determinate" value={parseInt(activeRatio)} color="secondary" />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Grid container alignItems="center" spacing={1}>
                                            <Grid item sm zeroMinWidth>
                                                <Typography variant="body2">Recovery Ratio</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography variant="body2" align="right">
                                                    {recoveredRatio}%
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={12}>
                                                <LinearProgress variant="determinate" value={parseInt(recoveredRatio)} color="primary" />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Grid container alignItems="center" spacing={1}>
                                            <Grid item sm zeroMinWidth>
                                                <Typography variant="body2">Case Fatality Ratio</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography variant="body2" align="right">
                                                    {fatalityRatio}%
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={12}>
                                                <LinearProgress variant="determinate" value={parseInt(fatalityRatio)} color="secondary" />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Grid container alignItems="center" spacing={1}>
                                            <Grid item sm zeroMinWidth>
                                                <Typography variant="body2">Vaccinated Ratio</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography variant="body2" align="right">
                                                    {vaccinatedRatio}%
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={12}>
                                                <LinearProgress variant="determinate" value={parseInt(vaccinatedRatio)} color="primary" />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12}>
                <StateWiseDataCard title="State-wise Data" />
            </Grid>
        </Grid>
    );
};

export default Default;
